/* Intersect.css */

.intersect-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    width: 100%;
    background-color: #f0f0f0;

  }
  
  .intersect-header {
    display: flex;
    height: 4rem;
    width: 100%;
    border-bottom: 2px solid black;
    background-color: #19b6f0; 
    align-items: center;
    justify-content: center;
  }
  
  .intersect-content {
    margin-top: 1rem;
  }
  
  .intersect-row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    width: 100%;
  }
  
  .intersect-item {
    display: flex;
    align-items: flex-start;
    justify-self: flex-start;
    width: 40%;
    padding-left: 1.5rem;
  }
  
  #file1 {
    width: 100%;
    height: 100%;
  }
  
  .intersect-item2 {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    width: 60%;
    padding-right: 1.5rem;
  }
  
  .intersect-row2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    width: 100%;
  }
  
  .intersect-item3 {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    width: 40%;
    padding-left: 1.5rem;
  }
  
  .intersect-item4 {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    width: 60%;
    padding-right: 1.5rem;
  }
  
  #file2 {
    width: 100%;
    height: 100%;
  }
  
  .intersect-button {
    display: flex;
    align-self: center;
    justify-self: center;
    width: 6rem;
    justify-content: center;
    align-items: center;
    height: 3rem;
    background-color: #f0f0f0;
    border-radius: 5px;
  }
  
  .intersect-alert {
    margin-top: 10px;
  }
  
  /* Styling for Alert */
  .MuiAlert-root {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2; /* Higher z-index */
    width: 200px; /* Adjust width as needed */
  }
  
  .buffer-content::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1; /* Lower z-index */
    pointer-events: none;
  }
  
  .buffer-content p {
    margin-top: 2rem; /* Adjust margin as needed */
  }
  